.footer{
    background-color: var(--background-color-2);
}
.footer--container{
    width: 100%;
    display: flex;
}
.footer__header{
    display: flex;
    flex-direction: column;

&--item1{
    font-family: var(--font-family-headline);
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 21px;
    text-transform: uppercase;
    padding-left: 22px;
    color: var(--text-color-3);
}
p:nth-child(2n) {
    margin-bottom: 17px;
}
&--item2{
    font-family: 'Titan One';
    font-style: normal;
    font-weight: 400;
    font-size: 34px;
    padding-left: 20px;
    line-height: 39px;
    letter-spacing: 0.04em;
    text-transform: uppercase;
    color: var(--text-color-2);
}
}
/*social media*/
.footer__social{
    max-width: 100%;

&-list{
    display: flex;
    flex-direction: row;
    justify-content: right;
    margin-right: 20px;
    list-style: none;
}
&-item{
    margin-left: 28px;
    display: flex;
    justify-content: center;
    width: 34px;
    height: 34px;
}
&-icon{
    padding: 7px;
    fill: #fff;
    background-color: var(--icon-background-color);
    border-radius: 50%;
    
}

&-svg{
    width: 20px;
    height: 20px;
    }
}
/*social media*/
.footer__contact{
    display: flex;
    list-style: none;
    justify-content: flex-end;

&-link {
    font-family: var(--font-family-main);
    font-style: normal;
    font-weight: 700;
    font-size: 12px;
    line-height: 186%;
    /* or 22px */
    text-align: right;
    color: var(--text-color-4);
}
&--span {
    font-family: var(--font-family-main);
    font-style: normal;
    font-weight: 700;
    font-size: 12px;
    line-height: 186%;
    /* or 22px */
    text-align: right;
    color: var(--text-color-2);
        }
        }
    

//desktop
@media screen and (min-width: 1280px) {
    .footer__header{
        max-width: 100%;
               

    &--item1{
    display: inline-flex;
    font-size: 30px;
    line-height: 34px;
    letter-spacing: 0.04em;
    color: var(--text-color-3);
    margin-bottom: 16px;
    }
    p {
        margin-right: 5px;
    }
    &--item2{
        display: inline-flex;
        font-size: 58px;
        line-height: 66px;
        letter-spacing: 0.06em;
        color: var(--text-color-2);
    }
}

    .footer {
        max-width: 100%;
    &--container{
        justify-content: space-between;
    }
    &__social{
        display: flex;
        flex-direction: column;
    }
    &__social-list {
        margin-right: 121px;
        margin-bottom: 50px;
    }

    &__social-item:hover{
        cursor: pointer;
    }
    &__social-item{
        margin-left: 24px;
        }

    &__social-icon:hover{
        color: #fff;
        background-color: var(--text-color-2);
    }

    &__contact{
        margin-top: 13px;
    }
    &__contact-link{
        font-size: 16px;
    line-height: 186%;
    /* identical to box height, or 30px */
    letter-spacing: 0.02em;
    margin-right: 116px;
    }
    &--span{
        font-size: 16px;
    }
    }       
}

//tablet
@media screen and (min-width: 768px) and (max-width: 1279px) {
    .footer{
        width: 100%;
    &--container{
        display: flex;
        flex-direction: row;
        justify-content: space-between;
    }
    
    &__header--item1{
    font-size: 26px;
    line-height: 30px;
    letter-spacing: 0.04em;
    color: var(--text-color-3);
  }
    &__header--item2{
        font-size: 48px;
        line-height: 55px;
        letter-spacing: 0.06em;
        color: var(--text-color-2);
    }
    /*social-media*/
    &__social{
        display: flex;
        flex-direction: column;
    }
    &__social-list {
        margin-right: 21px;
        margin-bottom: 46px;
    }
    &__social-item{
        margin-left: 30px;
        }
    &__contact{
        margin-right: 21px;
    }
    &__contact-link{
    font-size: 14px;
}
}
}
//mobile
@media screen and (min-width: 320px) and (max-width: 767px) {
    .footer{
        width: 100%;
    &--container{
        flex-direction: column;
    }
    &__header{
        flex-direction: column;
        align-items: flex-start;
        margin-bottom: 43px;
    }
    &__social-list {
        margin-bottom: 46px;
        margin-right: 5px;
}
    &__contact{
        margin-right: 5px;
    }
}
    } 