.page-header {
    margin: 0 auto;
    padding: 0 auto;
    min-height: 489px;
    display: flex;
    position: relative;
    background-color: var(--background-color-3);
    margin-bottom: 66px;
}

.header-uppercontent{
    display: flex;
    position: relative;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 130px;

    &__logo{
        margin-top: 6px;
    }

    &__navigation {
            display: none;
            visibility: hidden;
        }
    &__button{
        display: none;
        visibility: hidden;
    }

    &__hamburger{
        display:flex;
        visibility: visible;
    }
}

.header-main{
    display: flex;
    position: relative;
    align-items: center;
    justify-content: space-between;

    &__button{
        height: 24px;
        width: auto;
        display: flex;
        align-items: center;
        text-align: center;
        justify-content: center;
        font-family: var(--font-family-main);
        font-weight: 400;
        font-size: 8px;
        line-height: 10px;
        border-radius: 22px;
        margin-left: 0;
        padding: 0;
        height: 24px;
        width: 82.8px;
    }

    &__title{
        z-index: 30;
        display: flex;
        flex-wrap: wrap;
        justify-content: left;
        align-items: flex-start;
        text-align: left;
        width: 218px;
        height: 106px;

        &--white{
            text-align: left;
            color: var(--text-color-1);
            font-family: var(--font-family-headline);
            font-style: normal;
            font-weight: 400;
            font-size: 26px;
            line-height: 30px;
            letter-spacing: 0.04em;
            text-transform: uppercase;
        }
        &--red{
            color: var(--text-color-2);
            font-family: var(--font-family-headline);
            font-style: normal;
            font-weight: 400;
            font-size: 48px;
            line-height: 45.84px;
            letter-spacing: 0.04em;
            text-transform: uppercase;
        }
    }

    &__img{
        z-index: 20;
        position:absolute;
        left: 15px;
        bottom: -125px;
        border-radius: 50%;
        height: 344px;
        background-color: var(--background-color-4);
        &--ice{
            position: relative;
            padding:9px 94px 75px 81px ;
        }
    }

    &-info{
        position: relative;
        display: none;
        visibility: hidden;
        flex-wrap: wrap;
        flex-direction: row;

        &__box{
            position: relative;
            &--1{
                position: absolute;
                top: -90px;
                right: 20px;
                border-radius: 50%;
                width: 16px;
                height: 16px;
                background-color: var(--text-color-1);
                margin-bottom: 41px;
            }
            &--2 {
                position: absolute;
                top: -30px;
                right: 20px;
                border-radius: 50%;
                width: 16px;
                height: 16px;
                background-color: var(--text-color-1);
            }
        }

        &__text{
            position: absolute;
            right: 5px;
            display: flex;
            flex-wrap: wrap;
            flex-direction: column;
            font-family: var(--font-family-main);
            color: var(--text-color-1);
            font-style: normal;
            font-weight: 400;
            font-size: 8px;
            line-height: 10px;
            text-align: right;
            &--16{
                font-family: var(--font-family-headline);
                color: var(--text-color-2);
                font-style: normal;
                font-weight: 400;
                font-size: 28px;
                line-height: 32px;
                text-align: right;
                letter-spacing: 0.04em;
                text-transform: uppercase;
            }
            &--23 {
                font-family: var(--font-family-headline);
                color: var(--text-color-2);
                font-style: normal;
                font-weight: 400;
                font-size: 28px;
                line-height: 32px;
                text-align: right;
                letter-spacing: 0.04em;
                text-transform: uppercase;
            }
        }
    }
}

.header-lowercontent {
    display: none;
    position: relative;
    margin-right: 34px;
    margin-left: 34px;
    align-items: center;
    justify-content:flex-start;

    &__text {
        z-index: 50;
        display: flex;
        flex-direction: column;
        align-content: flex-start;
        align-items: flex-start;

            
        &--description {
            font-family: var(--font-family-main);
            color: var(--text-color-1);
            font-style: normal;
            font-weight: 400;
            font-size: 8px;
            line-height: 150%;
            width: 114px;
            height: 48px;
            align-self: flex-end;
            margin-top: 0px;
            }
    }

    &__button {
        margin-top: 20px;
        align-self: flex-start;
        margin-left: 0;
        padding-left: 0;
        background-color: transparent;
    }

    &__img{
        &--tasty{
            margin-left: auto;
            z-index: 50;
            align-self: flex-end;
        }
    }
}

@media screen and (min-width: 1280px) {
